.partnership {
  padding: 26px 20px;
  display: flex;
  justify-content: space-between;
  background-color: @lightgrey;

  &--light-theme {
    background-color: @white;
  }

  @media (min-width: @tablet-width) {
    padding: 60px 70px;
  }

  @media (min-width: @desktop-width) {
    padding: 60px 80px;
    width: 565px;
    background-color: @white;
  }
}

.partnership__title {
  margin: 0;
  padding: 0;
  font-family: @oswald;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.partnership__adress {
  margin: 0;
  padding: 0;
  font-family: @lato;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: @specialgrey;
}
