.stats__wrapper-primary {
  display: flex;
  justify-content: space-between;
  padding-bottom: 20px;
}

.stats__content {
  padding: 0;
  margin: 0;
  font-family: @oswald;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;

  &--big {
    font-size: 24px;
    line-height: 24px;
  }

  &--small {
    font-size: 14px;
    line-height: 20px;
  }
}

.stats__description {
  padding: 0;
  margin: 0;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: @specialgrey;
  text-transform: lowercase;
  text-align: center;
}
